// 

import React, { Fragment, useState, useEffect } from 'react';
import "./rollInfo.scss";
import Swiper from "swiper";
import "../../../../../node_modules/swiper/dist/css/swiper.min.css";

import headPortrait from "assets/img/picture/head-portrait.jpg"; // 

const RollInfo = () => {
  useEffect(() => {
    let rollInfoSwiper = new Swiper('.roll-info-swiper-container', {
      direction: 'vertical',
      spaceBetween: 0,
      loop: true,
      autoplay: true,
    });
    return () => {
      rollInfoSwiper.destroy(true);
    }
  });

  return(<div className="roll-info">
    <span className="title"></span>
    <div className="swiper-container roll-info-swiper-container swiper-no-swiping ">
      <div className="swiper-wrapper">
        <div className="swiper-slide"><img src={headPortrait} alt="" />XXX&nbsp;205&nbsp;100</div>
        <div className="swiper-slide"><img src={headPortrait} alt="" />YYY&nbsp;3010&nbsp;50</div>
        <div className="swiper-slide"><img src={headPortrait} alt="" />KKK&nbsp;1020&nbsp;150</div>
      </div>
    </div>
  </div>)
}

export default RollInfo;