// 

import React from 'react';
import "./voucherCenter.scss";
import { NavBar, Icon, WhiteSpace } from 'antd-mobile';
import ShareIconBlock from "common/shareIconBlock/ShareIconBlock"; // 
import RollInfo from "./common/rollInfo/RollInfo"; // 
import PhoneNumber from "./common/phoneNumber/PhoneNumber"; // 
import AddUpSave from "./common/addUpSave/AddUpSave"; // 
import RechargeCoupon from "./common/rechargeCoupon/RechargeCoupon"; // 
import RechargeNumber from "./common/rechargeNumber/RechargeNumber"; // 
import MoreRecharge from "./common/moreRecharge/MoreRecharge"; // 

const VoucherCenter = (props) => {
  return (<div className="voucher-center">
    <NavBar
      mode="light"
      className="voucher-center-navbar"
      icon={<Icon type="left" color="#868480" />}
      onLeftClick={() => {props.history.goBack();}}
      rightContent={<ShareIconBlock className="icon-but" />}
    ></NavBar>
    <div className="voucher-center-box">
      <RollInfo />
      <WhiteSpace />
      <PhoneNumber />
      <WhiteSpace />
      <AddUpSave />
      <WhiteSpace />
      <RechargeCoupon />
      <WhiteSpace />
      <RechargeNumber couponJson={{id:0, price: 0.1, condition: 50}} />
      <WhiteSpace />
      <MoreRecharge />
    </div>
  </div>)
}

export default VoucherCenter;